<template>
  <div>
    <JqxButton ref="button" :width="140" :height="15" @click="clearEmptyCollection()">Clear Empty</JqxButton>
    <JqxButton ref="button" :width="140" :height="15" @click="updateCollection()">Add</JqxButton>
    <JqxButton
      ref="button"
      :width="140"
      :height="15"
      @click="update1500Collection()"
    >Add 1500 New Rows</JqxButton>
    <JqxButton ref="button" :width="140" :height="15" @click="refreshCollection()">Refresh</JqxButton>

    <JqxButton ref="button" :width="140" :height="15" @click="saveAll()">Save All</JqxButton>

    <JqxNotification
      ref="msgNotification"
      :width="250"
      :position="'top-right'"
      :opacity="0.9"
      :autoOpen="false"
      :animationOpenDelay="800"
      :template="'info'"
    >
      <div>{{ message }}</div>
    </JqxNotification>

    <span>RowCount: {{ translation.length }}</span>

    <div>
      <div>Language Version: {{ app_version.version_number }}</div>
      <JqxButton ref="button" :width="140" :height="15" @click="updateLanguageVersion()">Update!</JqxButton>
    </div>

    <div>
      <span style="color: red">Message: {{ message }}</span>
    </div>

    <JqxGrid
      ref="myGrid"
      :width="2000"
      :theme="'material-green'"
      :source="dataAdapter"
      :columns="columns"
      :editable="true"
      :selectionmode="'multiplecellsadvanced'"
      @cellendedit="onCellendedit($event)"
    />
  </div>
</template>

<script>
import JqxGrid from "jqwidgets-scripts/jqwidgets-vue/vue_jqxgrid.vue";
import JqxButton from "jqwidgets-scripts/jqwidgets-vue/vue_jqxbuttons.vue";
import JqxNotification from "jqwidgets-scripts/jqwidgets-vue/vue_jqxnotification.vue";

export default {
  components: {
    JqxGrid,
    JqxButton,
    JqxNotification
  },
  data: function() {
    return {
      translation: [],
      app_version: {
        version_number: "Loading.."
      },
      message: "No Message",
      // eslint-disable-next-line
      dataAdapter: new jqx.dataAdapter(this.source),
      columns: [
        { text: "#", datafield: "row_index", width: 70 },
        { text: "ID", datafield: "document_id", width: 100, hidden: true },
        { text: "String Code", datafield: "string_code", width: 100 },
        { text: "Indonesia", datafield: "indonesia", width: 150 },
        { text: "English", datafield: "english", width: 150 },
        { text: "German", datafield: "german", width: 150 },
        { text: "China", datafield: "china", width: 150 },
        { text: "Japan", datafield: "japan", width: 150 },
        { text: "Korea", datafield: "korea", width: 150 },
        { text: "Rusia", datafield: "rusia", width: 150 },
        { text: "Parameter", datafield: "parameter" }
      ]
    };
  },
  methods: {
    onCellendedit: function(event) {
      var args = event.args;
      var columnDataField = args.datafield;
      var rowIndex = args.rowindex;
      var cellValue = args.value;
      var oldValue = args.oldvalue;
      var documentId = this.$refs.myGrid.getcell(rowIndex, "document_id").value;
      var stringCode = this.$refs.myGrid.getcell(rowIndex, "string_code").value;
      var indonesia = this.$refs.myGrid.getcell(rowIndex, "indonesia").value;
      var english = this.$refs.myGrid.getcell(rowIndex, "english").value;
      var german = this.$refs.myGrid.getcell(rowIndex, "german").value;
      var china = this.$refs.myGrid.getcell(rowIndex, "china").value;
      var japan = this.$refs.myGrid.getcell(rowIndex, "japan").value;
      var korea = this.$refs.myGrid.getcell(rowIndex, "korea").value;
      var rusia = this.$refs.myGrid.getcell(rowIndex, "rusia").value;
      var updatedValue = {
        row_index: rowIndex,
        string_code: stringCode,
        indonesia: indonesia,
        english: english,
        german: german,
        china: china,
        japan: japan,
        korea: korea,
        rusia: rusia
      };

      updatedValue[columnDataField] = cellValue;

      this.translation[rowIndex] = updatedValue;
      return true;
    },
    updateCollection: function() {
      this.translation.push({
        row_index: this.translation.length,
        string_code: "NEW_CODE"
      });
      this.source.localdata = this.translation;
      this.$refs.myGrid.updatebounddata("cells");
    },
    update1500Collection: function() {
      var arr = this.translation;
      for (var i = 0; i < 1500; i++) {
        arr.push({
          row_index: i + 1,
          string_code: "NEW_CODE"
        });
      }

      this.source.localdata = this.translation;
      this.$refs.myGrid.updatebounddata("cells");
    },
    clearEmptyCollection: function() {
      // for (var i = 0; i < this.translation.length; i++) {}

      this.translation = this.translation.filter(function(value) {
        return value["string_code"] != "NEW_CODE";
      });

      for (var i = 0; i < this.translation.length; i++) {
        this.translation[i]["row_index"] = i;
      }

      this.source.localdata = this.translation;
      this.$refs.myGrid.updatebounddata("cells");
    },
    refreshCollection: function() {
      this.source.localdata = this.translation;
      this.$refs.myGrid.updatebounddata("cells");
    },
    saveAll: async function() {
      var items = [];

      for (var rowIndex = 0; rowIndex < this.translation.length; rowIndex++) {
        var documentId = this.$refs.myGrid.getcell(rowIndex, "document_id")
          .value;
        var stringCode = this.$refs.myGrid.getcell(rowIndex, "string_code")
          .value;
        var indonesia = this.$refs.myGrid.getcell(rowIndex, "indonesia").value;
        var english = this.$refs.myGrid.getcell(rowIndex, "english").value;
        var german = this.$refs.myGrid.getcell(rowIndex, "german").value;
        var china = this.$refs.myGrid.getcell(rowIndex, "china").value;
        var japan = this.$refs.myGrid.getcell(rowIndex, "japan").value;
        var korea = this.$refs.myGrid.getcell(rowIndex, "korea").value;
        var rusia = this.$refs.myGrid.getcell(rowIndex, "rusia").value;

        var updatedValue = {
          string_code: stringCode,
          indonesia: indonesia,
          english: english,
          german: german,
          china: china,
          japan: japan,
          korea: korea,
          rusia: rusia
        };

        items.push(updatedValue);
      }

      this.$http.headers.common["Access-Control-Allow-Origin"] = true;
      this.$http.headers.common["Access-Control-Allow-Origin"] = "*";
      this.$http.options.emulateJSON = true;

      var url =
        "http://21flutter.com/translation/public/api/custom/translation/updateTranslation";

      this.$http.post(
        url,
        {
          items: items
        },
        {
          headers: {
            Accept: "application/json"
          }
        }
      );
      // this.axios.defaults.headers.post["Content-Type"] =
      //   "application/x-www-form-urlencoded";
      // this.axios
      //   .post(
      //     "http://localhost/translation/public/api/custom/translation/updateTranslation",
      //     // "http://21flutter.com/translation/public/api/custom/translation/updateTranslation",
      //     // {
      //     //   headers: {
      //     //     "Content-Type": "application/json"
      //     //   }
      //     // },
      //     {
      //       headers: {
      //         "Content-Type": "application/json"
      //       }
      //     },
      //     {
      //       items: items
      //     }
      //   )
      //   .then(response => {
      //     this.app_version = {
      //       version_number: response.data.version_number
      //     };
      //   });
    },
    updateLanguageVersion: function() {
      var currentVersion = this.app_version.version_number;

      this.axios
        .get(
          "http://21flutter.com/translation/public/api/custom/translator_version/updateVersion"
        )
        .then(response => {
          console.log(response.data);
          this.getVersion();
        });
    },
    getVersion: function() {
      this.axios
        .get(
          "http://21flutter.com/translation/public/api/custom/translator_version/getVersion"
        )
        .then(response => {
          this.app_version = {
            version_number: response.data.version_number
          };
        });
    }
  },
  beforeCreate: function() {
    this.source = {
      // localdata: [
      //   {
      //     string_code: "D0045_OLD",
      //     indonesia: "Selamat Pagi",
      //     english: "Good morning",
      //     china: "早安",
      //     japan: "おはようございます",
      //     korea: "좋은 아침",
      //     rusia: "доброе утро",
      //     parameter: "<button>parameter</button>"
      //   },
      // ],
      localdata: this.translation,
      datatype: "array"
    };
  },
  mounted: function() {
    this.getVersion();
  }
};
</script>

<style src='./assets/styles/jqwidgets/jqx.base.css'></style>
<style src='./assets/styles/jqwidgets/jqx.material-green.css'></style>